.App {
  text-align: center;
}

.App-logo {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 70px;
  width: 150px;
  background-image: url('https://www.freepnglogos.com/uploads/usps-png-logo/usps-emblem-logo-png-4.png');
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.megnifier-glass{
  cursor: pointer;
}

.example{
  font-size: 13px;
  margin-top: 1px;
}

.App-header {
  background-color: #cebb5a;
  padding: 7px;
  display: flex;
  flex-direction: column;

  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@import url(https://fonts.googleapis.com/css?family=Open+Sans);

body{
  background: #f2f2f2;
  font-family: 'Open Sans', sans-serif;
}

.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #cebb5a;
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: #cebb5a;
}

.searchTerm:focus{
  color: black;
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid #cebb5a;
  background: #cebb5a;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.tracking-info{
  margin-top: 3px;
  color:red;
  font-size: 14px;
  border: 1px solid lightgray;
}

.wrap{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}